import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import { animateScroll as scroll } from "react-scroll";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import { setPromise } from "../Reducer/jungleSwapSlice";
import CheckoutForm from "./CheckoutForm";

const CheckoutPage = () => {
  const dispatch = useDispatch();

  // Load Stripe and scroll to top as soon as page loads
  useEffect(
    () => {
      const promise = loadStripe("pk_test_51IQBsPA6EAM4YnfDyrjHWnLHzZ5KkI9tsERzYhBGVoctZBrFUb4Sda035HvcQKpp7thFiqW6QmO8ytPbOAMTg33z00cHvcbojv");
      dispatch(setPromise(promise));
      scroll.scrollToTop()
    },
    []
  );

  return (
    <div className="container row mt-5">
      <div className="mt-5 col-11 col-md-6 offset-1 offset-md-5 App">
        <Elements>
          <CheckoutForm />
        </Elements>
      </div>
    </div>
  );
}

export default CheckoutPage;